import React, { Component } from "react";
import FriendCard from "./components/FriendCard";
import Wrapper from "./components/Wrapper";
import Title from "./components/Title";
import friends from "./friends.json";

class App extends Component {
  
  state = {
    originalFriends:friends,
    friends,
    score:0
  };

  removeFriend = id => {
  
    let correct = false
    let score = this.state.score

    this.state.friends.forEach(element => {
      if(id === element.id){
        correct=true
        score++
      }
    });
    
    console.log(correct)
    console.log(score)
    let friends = this.state.friends.filter(friend => friend.id !== id);

    if(correct===false){
      score = 0
      friends = this.state.originalFriends
    }

    this.setState({ friends });
    this.setState({ score });

  };

  render() {
    return (
      <Wrapper>
        <Title>Clicky Game: Score: {this.state.score}</Title>
        {friends.map(friend => (
          <FriendCard
            removeFriend={this.removeFriend}
            id={friend.id}
            key={friend.id}
            image={friend.image}
          />
        ))}
      </Wrapper>
    );
  }
}

export default App;
